.sidebar {
  width: 250px;
  min-height: calc(100vh - 60px);
  /* max-height: calc(100vh - 60px); */
  height: 100%;
  overflow-y: auto;
  background-color: var(--backgroundColor);
  position: fixed;
  left: 0;
  top: 60px;
}

.sidebar.left {
  position: fixed;
  left: 0;
  transform: translateX(-100%);
  opacity: 1;
  transition: 0.3s ease;
  z-index: 5;
}

.sidebar.left.show {
  transform: translateX(0);
  opacity: 1;
}

.sidebar.inactive {
  width: 80px;
}

.sidebar-background {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.514);
  z-index: 4;
  opacity: 0;
  transition: 0.3s ease;
  pointer-events: none;
}

.sidebar-background.show {
  opacity: 1;
  pointer-events: all;
}

.categories {
  list-style-type: none;
  padding: 10px;

  li {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    position: relative;
    margin-bottom: 10px;
    height: 21px;
    padding: 30px 0;

    i {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translateY(-50%);
      pointer-events: none;
    }

    p {
      pointer-events: none;
      margin: 0;
      font-size: 20px;
      width: 60px;
      color: var(--fontColor);
      font-family: "Barlow Condensed", sans-serif;
      font-weight: 500;
    }

    p.inactive {
      display: none;
    }
  }

  li:hover,
  li.active {
    background-color: var(--backgroundColorLight);
  }

  li i {
    filter: var(--filterLight);
    font-size: 20px;
  }

  li.active i {
    filter: var(--filterRed);
  }
}
