  --fontColor: #000000;
  --filter: brightness(0) saturate(100%) invert(35%) sepia(57%) saturate(4604%)
    hue-rotate(340deg) brightness(98%) contrast(112%);
  /* --filterLight: brightness(0) saturate(100%) invert(0%) sepia(93%) saturate(0%) hue-rotate(267deg) brightness(106%) contrast(106%); */
