

.search-container {
    width: calc(100vw - 250px);
    min-height: calc(100vh - 60px);
    max-height: calc(100vh - 60px);
    background-color: var(--backgroundColor);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    gap: 30px;
  }
  
  .search-container.big {
    width: calc(100vw - 80px);
  }

  .search-card {
    display: flex;
    width: calc(70vw - 250px);
    height: 30vh;
    gap: 20px;

    h2 {
        font-size: 20px;
    }

    img {
        height: 100%;
        aspect-ratio: 4 / 2.5;
        border-radius: 12px;
        object-fit: cover;
      }
  }

  .search-card-text-container {
    display: flex;
    flex-direction: column;
    gap: 10px;

    h2 {
        font-size: 20px;
    }

    h2, h3, p {
        margin: 0;
    }
  }