.nav {
  max-width: 100vw;
  padding-right: 20px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  background-color: var(--backgroundColor);
}

.logo-container {
  margin: 0 10px;
  height: 60px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 12px;

  .burger-menu {
    margin-right: 20px;
    position: relative;
    border-radius: 50%;
    height: 44px;
    width: 44px;
  }

  .burger-menu:hover {
    background-color: rgb(156, 156, 156);
  }

  .burger-line {
    height: 1px;
    width: 20px;
    background-color: black;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
  }

  .burger-line:nth-of-type(1) {
    top: 16px;
  }

  .burger-line:nth-of-type(2) {
    top: 28px;
  }

  h1 {
    cursor: pointer;
    color: var(--fontColor);
  }
}

.logo {
  height: 30px;
  width: 40px;
  background-size: 100%;
  /* background-image: url("../../public/images/logo/logo-red.png"); */
  background-image: var(--logoLight);
  background-repeat: no-repeat;
  background-position: 50%;
  cursor: pointer;
}

.search-box {
  width: 30%;
  height: 40px;
  border: 1px solid rgb(148, 148, 148);
  border-right: none;
  border-radius: 20px;
  display: flex;
  align-items: center;
  padding-left: 10px;
  float: right;
  position: relative;

  input {
    all: unset;
    margin-left: 10px;
    line-height: 30px;
    width: 70%;
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
  }

  i:nth-of-type(2) {
    position: absolute;
    right: 90px;
    cursor: pointer;
    border-radius: 50%;
    height: 38px;
    width: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  i:nth-of-type(2):hover {
    background-color: rgba(0, 0, 0, 0.651);
  }

  i {
    filter: var(--filterLight);
  }

  a {
    height: 100%;
    width: 80px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-right: 1px solid rgb(148, 148, 148);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--backgroundColorLight);
    z-index: 0;
    position: absolute;
    top: 0;
    right: 0;
  }
}

