.appearance-container {
  position: absolute;
  transform: translateX(-20px);
  width: 250px;
  box-shadow: 0 0 5px black;
  top: 90%;
  right: 10px;
  background-color: white;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  display: none;

  h2 {
    text-align: center;
    font-weight: 400;
  }

  hr {
    border: 1px solid var(--backgroundColorLight);
  }

  ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0;
    gap: 20px;

    li {
      font-size: 16px;
      display: flex;
      font-weight: 400;
      font-style: normal;

      span:nth-of-type(1) {
        width: 30%;
        display: flex;
        justify-content: center;
      }

      span:nth-of-type(2) {
        width: 70%;
      }

      i {
        font-size: 24px;
        opacity: 0;
      }

      i.active {
        opacity: 1;
      }
    }
  }
}

.appearance-container.active {
  display: block;
}
