.home-page-container {
  width: calc(100vw - 250px);
  min-height: calc(100vh - 60px);
  max-height: calc(100vh - 60px);
  background-color: var(--backgroundColor);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-template-rows: 0fr;
  padding: 20px;
  gap: 30px 16px;
}

.home-page-container.big {
  width: calc(100vw - 80px);
}

.video-card {
  margin: 0;
  height: fit-content;
  max-width: 600px;

  img {
    width: 100%;
    aspect-ratio: 4 / 2.5;
    border-radius: 12px;
    object-fit: cover;
  }
}

.video-card-container {
  display: flex;
  align-items: center;
}

.video-card-text-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;

  h2,
  h3,
  p {
    margin: 0;
    color: var(--fontColor);
  }

  h2 {
    overflow: hidden;
    font-size: 16px;
  }

  h3 {
    font-size: 14px;
  }

  p {
    font-size: 12px;
  }
}

