
.single-video-container {
  width: 90%;
  height: max-content;
  margin: auto;
  display: flex;
}

.video, .video-about, .comment-section {
    width: 98%;
    height: fit-content;
    margin: 20px auto;
    color: var(--fontColor);
}

.comment-section {
  position: relative;
}

.video-buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 10px;

    h2, p {
        margin: 0;
    }
}

iframe {
  all: unset;
  width: 100%;
  border-radius: 12px;
  aspect-ratio: 16 / 9;
}

.current-video-container {
  width: calc(100% - 400px);
  height: 100vh;
}

.related-videos {
  width: 500px;
  height: 100vh;
  color: var(--fontColor);
}

.video-buttons-item {
    display: flex;
    align-items: center;
    gap: 20px;
}

.subscribe-btn {
    padding: 10px 30px;
    border-radius: 20px;
    background-color: black;
    color: white;
    font-weight: bold;
}

.channel-name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.comment {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 50px;
}

.comment-text {
  display: flex;
  flex-direction: column;

  h3, p {
    margin: 0;
  }

  h3 {
    font-size: 16px;
  }

  p {
    width: 65%;
    font-size: 18px;
    overflow-x: hidden;
  }

  .like-dislike-btn {
    margin-top: 10px;
    display: flex;
    gap: 20px;
  }

  .comment-time {
    display: inline;
    font-size: 12px;
    margin-left: 10px;
    color: grey;
  }
}

.related-video {
  display: flex;
  margin: 20px auto;
  align-items: flex-start;
  gap: 10px;
  height: 120px;

  p {
    margin: 0 0 2px;
  }
}

.related-img {
  height: 120px;
  aspect-ratio: 5 / 3;
  object-fit: cover;
  border-radius: 12px;
}

.related-title {
  font-weight: bold;
  font-size: 16px;
}

.related-channel, .related-info {
  font-size: 14px;
}

.like-btn-container {
  display: flex;
  margin-right: 50px;
  font-size: 20px;
}

.like-container, .dislike-container {
  background-color: rgb(230, 230, 230);
  padding: 10px 20px;
}

.like-container {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  position: relative;
  display: flex;
  gap: 20px;
  align-items: center;
}

.like-container::after {
  content: "";
  position: absolute;
  height: 30px;
  width: 2px;
  background-color: rgb(114, 114, 114);
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.dislike-container {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}


.icon-like {
  background-image: url("../../public/images/icons/iconLike.png");
  background-size: contain;
  filter: var(--filterLight);
}

.icon-dislike {
  background-image: url("../../public/images/icons/iconLike.png");
  background-size: contain;
  transform: scale(-1);
  filter: var(--filterLight);
}

.comment-dots {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 5%;

  div {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    transform: scale(0.3);
    background-color: black;
  }
}

.video-desc-container {
  margin-top: 20px;
  border-radius: 20px;
  width: 100%;
  background-color: var(--backgroundColorLight);
  padding: 1px 10px;

  .video-desc-stats {
    font-weight: bold;
    display: flex;
    gap: 20px;
  }

  .desc-text {
    width: 80%;
  }
}